/* *****************
Setting page styles -- Below this line
******************   */

.settings {
  align-items: center;
  display: flex;
  height: 100vh;
  justify-content: center;
  width: 100vw;
  position: absolute;
  top: 0;
  left: 0;
}
.settings h1 {
    color:yellow;
    margin-bottom: 60px;
    font-family: cursive;
}
.Modal {
  background: #3E363F;
  border-radius: 2px;
  padding: 20px;
  width: 320px;
  height:400px;
}

.Input {
  display: flex;
  flex-direction: row-reverse;
  border-bottom: 1.5px solid #ffff00ec;
  padding-bottom: 18px;
  margin-bottom: 35px;
}
.Input  {
  outline: none;
  border: 0;
  color:red;
  background: transparent;
  font-family: "Lato", sans-serif;
  flex: 1 0 auto;
  font-size: 16px;
  font-weight: 800;
}
.Input:focus ~ label {
  opacity: 1;
}
.Input label {
  font-size: 13px;
  font: '900 17px "Font Awesome 5 Free"';
  opacity: .1;
  -webkit-transition: opacity .5s ease;
  transition: opacity .5s ease;
}
.Input label::before {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #FFFCE8;  
  height: 30px;  
  width: 30px;
  
   
}


input[type='text'] ~ label::before {
  
  font-family: "Font Awesome 5 Free";
  content: "\F007";
  font-weight: 900;
}
 input[type='email'] ~ label::before {
 
  font-family: "Font Awesome 5 Free";
  content: "\F1FA";
  font-weight: 900;
}
 input[type='password'] ~ label::before {
  
  font-family: "Font Awesome 5 Free";
  content: "\F023";
  font-weight: 900;
}

button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  background: #DD403A;
  border: 0;
  border-radius: 3px;
  color: white;
  font-family: "Lato", sans-serif;
  font-size: 18px;
  font-weight: 500;
  text-transform: uppercase;  
  margin-top: 20px;
  outline: none;
  padding: 18px 9px 18px 11px;  
  
}

button:hover {
  -webkit-transform: scale(1.02);
          transform: scale(1.02);
}
button:active {
  -webkit-transform: scale(0.99);
          transform: scale(0.99);
}

